import React, { Component } from 'react';

import Board from '../components/Board';
import Graveyard from '../components/Graveyard';
import MoveList from '../components/MoveList';
import TurnSelector from '../components/TurnSelector';
import HistoryModel from '../models/HistoryModel';

export interface NewGameProps {
  match: any,
  history: any
}

export default class NewGameView extends Component<NewGameProps, any> {
    
    constructor(props : NewGameProps) {
        super(props);

        this.newGame = this.newGame.bind(this);
    }

    newGame() {
        fetch("http://localhost:8080/api/v1/games", {
            method: "POST",
            headers: {
                "Content-Type": "application/json"
            },    
            body: JSON.stringify({
                white: "HUMAN",
                black: "RANDBOT"
            })
        })
        .then(response => response.json())
        .then( response => {
            this.props.history.push("/play/" + response.id);
        })
    }

    render() {
        return (<div>
            <h2>New Game</h2> 
            
            <button onClick={this.newGame}>New Game</button>
                
             
        </div>)
    }
}

