import React, { Component } from 'react';
import Board from '../components/Board';
import GameModel from '../models/GameModel';
import Graveyard from '../components/Graveyard';
import MoveList from '../components/MoveList';
import PositionModel from '../models/PositionModel';
import { Link } from 'react-router-dom';

interface PlayGameProps {
    match: any
}

interface PlayGameState {
    game : GameModel

    selected?: PositionModel
    highlighted?: PositionModel[]
}

export default class PlayGameView extends Component<PlayGameProps, PlayGameState> {

    constructor(props : PlayGameProps) {
        super(props)

        this.state = {
            game: {
                id: "",
                board: {
                    id: "",
                    pieces: []
                },
                graveyard: [],
                moves: [],
                whitePlayer: "",
                blackPlayer: "",
                isWhiteWinner: false, 
                isGameOver: false
            },
        }
    
        var gameId = this.props.match.params.gameId;
        this.onBoardSquareClick = this.onBoardSquareClick.bind(this);

        this.onHandleMove = this.onHandleMove.bind(this);
    }

    loadGameHistory() { 
        var gameId = this.props.match.params.gameId;
        var turnCount = this.props.match.params.turnCount;
    
        fetch(`http://localhost:8080/api/v1/games/${gameId}`)
          .then( (response) => {
              return response.json()
          })
          .then( (json : GameModel) => {
            console.log(json);
            this.setState({game: json})
          }
        );
    }

    onBoardSquareClick(x : number, y : number, e : any) {
        var gameId = this.props.match.params.gameId;
        var selectedPosition : PositionModel = {x, y}
        
        fetch(`http://localhost:8080/api/v1/games/${gameId}/moves?x=${x}&y=${y}`)
            .then( (response) => {
                return response.json();
            })
            .then( (json: PositionModel[]) => {
                this.setState({
                    selected: selectedPosition,
                    highlighted: json
                })
            });

        return false;
    }

    componentDidMount() {
        this.loadGameHistory();
    }

    onHandleMove(from : PositionModel, to : PositionModel) {
        console.log("[+] Handle Move", from, to)
        var gameId = this.props.match.params.gameId;

        fetch(`http://localhost:8080/api/v1/games/${gameId}/moves`, {
            method: "POST",
            headers: {
                "Content-Type": "application/json"
            },    
            body: JSON.stringify({
                from, to
            })
        })
        .then(response => response.json())
        .then( (game : GameModel) => {
            this.setState({
                game: game,
                selected: undefined, 
                highlighted: []
            })
        })
    }

    render() {
        var gameId = this.props.match.params.gameId;

        if (this.state.game.isGameOver) {
            var winner = this.state.game.isWhiteWinner ? <p>White Wins!</p> : <p>Black wins!</p> 
            var historyUrl = `/history/${this.state.game.id}/0`
            return (<div>
                <h3>Game Over!!</h3>
                {winner}
                <p>Review the game! <Link to={historyUrl}>Game Analysis</Link></p>
            </div>)
        }
    
        return (
            <div>
            <h3>{this.state.game.whitePlayer} vs {this.state.game.blackPlayer}</h3>
            
            <div className="row">
            <div className="col-8">
                <Board 
                    gameId={gameId} 
                    onBoardSquareClick={this.onBoardSquareClick} 
                    pieces={this.state.game.board.pieces} 
                    perspective={"WHITE"}

                    selected={this.state.selected}
                    highlighted={this.state.highlighted}
                    
                    draggableColor={"WHITE"}
                    onHandleMove={this.onHandleMove}
                    // onDragPieceStart={this.onDragPieceStart}
                    // onDragPieceDrop={this.onDragPieceDrop}
                    ></Board> 
            </div>
    
            <div className="col-4">
                <Graveyard pieces={this.state.game.graveyard}></Graveyard>
                <MoveList moves={this.state.game.moves}></MoveList>
            </div>
        </div> 
        </div>)   
    }
}
